import React from "react";
import s from "./Header.module.scss";
import Navigation from "./../Navigation/Navigation";
import insta from "./../../assets/insta.png";
import messanger from "./../../assets/messanger.png";
import viber from "./../../assets/viber.png";
import phone from "./../../assets/phone.png";

function Header() {
  const getSocialBlock = () => {
    return (
      <div className={s.social}>
        <div>
          <a href="/#" draggable={false}>
            <img src={insta} alt="a" draggable={false} />
          </a>
        </div>
        <div>
          <a href="/#" draggable={false}>
            <img src={messanger} alt="a" draggable={false} />
          </a>
        </div>
        <div>
          <a href="/#" draggable={false}>
            <img src={viber} alt="a" draggable={false} />
          </a>
        </div>
        <div>
          <a href="/#" draggable={false}>
            <img src={phone} alt="a" draggable={false} />
          </a>
        </div>
      </div>
    );
  };
  return (
    <div className={s.Header}>
      <Navigation></Navigation>
      <div className={s.Head}>
        {getSocialBlock()}

        <div>
          {/*asfagsa*/}
          <div />
        </div>
      </div>
    </div>
  );
}

export default Header;
